.Container { width: 1200px; margin: 0px auto; overflow: hidden;}
.clear { clear: both; display: block; font-size: 0; height: 0; line-height: 0; overflow: hidden; }
.fl {float: left;}
.fr {float: right;}
// body {min-width: 1380px;}
body {min-width: 1200px; overflow-x: hidden;}
.abs {position: absolute; top: 0; left: 0; bottom: 0; right: 0; margin: auto;}
.el {word-break: keep-all; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}